<template>
	<div id="app">
		<div v-if="Login.status" class="ta-app" :class="[Config.Unfold ? 'ta-unfold' : 'ta-fold', 'ta-theme-'+Config.Settings.theme]">
			<my-head></my-head>

			<div class="ta-content">

				<my-sidebar></my-sidebar>
				<div class="ta-overlay" :class="{'active': Config.Unfold}" @click="fold" @touchstart="fold"></div>

				<section class="ta-content-wrapper">
					<div class="am-cf">
						<a href="javascript:;" class="am-btn am-fl history-prev-btn" @click="historyPrev">
							<i class="iconfont icon-fanhui"></i> 返回上一页
						</a>
						<!--<ol class="am-fl am-breadcrumb am-breadcrumb-slash am-margin-0">
							<li>
								<a href="#">首页</a>
							</li>
							<li class="am-active">内容</li>
						</ol>-->
					</div>

					<router-view></router-view>
				</section>

			</div>

			<my-toast></my-toast>
			<my-settings></my-settings>
		</div>

		<div v-else>
			<!-- 未登陆路由 -->
			<router-view :name="login"></router-view>
		</div>

	</div>

</template>

<script>
	import {
		mapGetters,
		mapActions
	} from 'vuex'

	import MyHead from 'components/Head'
	import MySidebar from 'components/Sidebar'
	import MySettings from 'components/Settings'
	import MyToast from 'components/Toast'
	import MyLogin from 'components/Login'

	export default {
		name: 'App',
		computed: {
			...mapGetters({
				Config: 'getConfig',
				Login: 'getLogin'
			})
		},
		components: {
			MyHead,
			MySidebar,
			MySettings,
			MyToast,
			MyLogin
		},
		methods: {
			...mapActions({
				fold: 'Fold'
			}),
			historyPrev() {
				this.$router.go(-1)
			}
		}
	}
</script>

<style lang="less">
	@import "assets/css/ta.less";
	@import "assets/css/main.less";
</style>


