<template>
	<div class="ta-app login-container">
		<div class="login-box">
			<div class="logo-img">
				<img src="../../static/images/logo2_03.png" alt="" />
			</div>
			<form class="am-form" id="login-form" onsubmit="return false;">
				<div class="am-form-group">
					<label><i class="am-icon-user"></i></label>
					<input type="text" name="userName" value="admin" maxlength="18" placeholder="输入账户" required />
				</div>

				<div class="am-form-group">
					<label><i class="am-icon-key"></i></label>
					<input type="password" name="password" value="admin" placeholder="输入密码" required />
				</div>
				<button class="am-btn am-btn-secondary" type="submit">登录</button>
			</form>
		</div>
	</div>
</template>

<script>
	import {
		mapGetters,
		mapActions
	} from 'vuex'

	export default {
		name: 'Login',
		data() {
			return {
				name:'1',
				text:'2'
			}
		},
		computed: {
			...mapGetters({
				Login: 'getLogin'
			})
		},
		methods: {
			...mapActions({
				login: 'login'
			}),
			submit() {
				this.login({name:'admin',password:'admin'})
				this.$router.push(this.$store.getters.getSidebar[0].link)
			}
		},
		mounted() {
			const self = this;
			const $form = $('#login-form');
			$form.find('input').on('focus', function(){
				let thisElm = this;
				if($.getSystem() != 'PC'){ // 解决移动端遮盖输入框事件
					$(window).one('resize', function(){
						thisElm.scrollIntoView()
					})
				}
			})
			
			$form.validator({
				submit: function(e, validity) {
					var formValidity = this.isFormValid();
					if(formValidity) {
						self.submit()
					}
					return false;
				}
			})
		}
	}
</script>

<style lang="less" scoped>
	.login-container {
		width: 100%;
		padding-top: 150px;
		transition: all .3s ease-in;
		background: url(../../static/images/login-bg.jpg);
	}
	
	.login-container .login-box {
		width: 326px;
		margin: 0 auto
	}
	
	.login-container .login-box .logo-img {
		width: 150px;
		margin: 0 auto
	}
	
	.login-container .login-box .am-form {
		margin-top: 68px
	}
	
	.login-container .login-box .am-form .am-form-group {
		overflow: auto;
		border-bottom: 2px #68c6de solid;
		margin-bottom: 30px
	}
	
	.login-container .login-box .am-form .am-form-group label {
		float: left
	}
	
	.login-container .login-box .am-form .am-form-group label i {
		font-size: 24px;
		color: #68c6de
	}
	
	.login-container .login-box .am-form .am-form-group input {
		width: 265px;
		float: left;
		margin-left: 10px;
		border: 0;
		background: 0 0
	}
	
	.login-container .login-box .am-form .am-btn-secondary {
		width: 100px;
		height: 3rem;
		background: #68c6de;
		border-color: #68c6de;
		border-radius: 27px;
		display: block;
		margin: 0 auto;
	}
	
	@media screen and (max-width:640px) {
		.login-container {
			padding: 15px 15px 0;
		}
		.login-container .login-box {
			width: 100%
		}
		.login-container .login-box .am-form {
			margin-top: 50px
		}
		.login-container .login-box .am-form .am-form-group input {
			width: 88%
		}
	}
</style>


