<template>
	<div class="am-g ta-g">
		<div class="am-u-md-8">
			<form class="am-form am-form-horizontal" id="config-form" autocomplete="off">
				<fieldset>
					<div class="am-form-group">
						<label>网站标题</label>
						<input type="text" name="WEB_TITLE" maxlength="10" placeholder="例如：优惠码系统">
					</div>
					<div class="am-form-group">
						<label>网站关键词</label>
						<input type="text" name="WEB_KEYWORDS" maxlength="18" placeholder="例如：优惠码，优惠码系统">
					</div>
					<div class="am-form-group">
						<label>网站描述</label>
						<textarea class="web-description" name="WEB_DESCRIPTION" maxlength="100"></textarea>
					</div>
					<div class="am-form-group">
						<label>网站备案</label>
						<input type="text" name="WEB_ICP" value="京ICP备110号">
					</div>
					<div class="am-form-group">
						<label>客服QQ</label>
						<input type="text" class="js-pattern-number" name="WEB_QQ" value="465040621" pattern="[1-9][0-9]{4,14}" maxlength="14" data-validation-message="QQ必须为 4-14 位数字">
					</div>
					<button type="submit" class="am-btn am-btn-primary">保存</button>
				</fieldset>
			</form>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'ConfigView',
		mounted() {
			$('#config-form').validator({
				onValid: function(validity) {
					$(validity.field).closest('.am-form-group').find('.am-alert').hide();
				},
				onInValid: function(validity) {
					let $field = $(validity.field);
					let $group = $field.closest('.am-form-group');
					let $alert = $group.find('.am-alert');
					// 使用自定义的提示信息 或 插件内置的提示信息
					let msg = $field.data('validationMessage') || this.getValidationMessage(validity);
					if(!$alert.length) {
						$alert = $('<div class="am-alert am-alert-danger"></div>').hide().
						appendTo($group);
					}

					$alert.html(msg).show();
				},
				submit: function(e, validity) {
					var formValidity = this.isFormValid();
					var $thisForm = $(e.target);
					if(formValidity) {
						// 验证通过
						console.log($thisForm.serialize())
					}
					return false;
				}
			});
		}
	}
</script>
<style type="text/css" scoped>
	.web-description {
		max-height: 200px;
	}
</style>


